	module.exports = {"data":{"markdownRemark":{"html":"<p>From that day, Marisa who came to visit the shrine every day as usual suddenly came to nothing. Reimu, while feeling lonely, was optimistically optimistic and bored with drinking tea, but Marlinsa's broom and hat were delivered from Shiratamaro to such a medium of Reimu.</p>\n<h2>- Toho Evil Rage -</h2>\n<p>Yomi says \"Miya fell in the garden\". The Reimu I thought suspicious would infiltrate for the first time in the Kiryu Rosary .... Perhaps this is said to be the origin of Miso, but the truth is a mystery.</p>","frontmatter":{"cover":null,"date":"2004-07-29T19:35:00+09:00","strDate":"2004/7/29","title":"It was more severe than someone's study"}}},"pathContext":{"path":"/en/blog/2004/07/29-誰かさんの書斎より酷かった/","langKey":"en"}}

